<template>
  <div id="app" class="container">
    <div class="section" id="index-banner" v-if="paso!==2">
      <div class="container">
        <div class="row" style="margin-bottom: 0;">
          <div class="col s12 m8 offset-m1 xl7 offset-xl1 center-on-small-only">
            <h1 class="header">Biblioteca</h1>
            <h4 class="light">Referencias incrustadas</h4>
          </div>
        </div>
      </div>
    </div>
    <div class="container">
      <div v-if="paso===0" class="center-align">
        <br>
        <div class="preloader-wrapper big active">
          <div class="spinner-layer spinner-blue">
            <div class="circle-clipper left">
              <div class="circle"></div>
            </div><div class="gap-patch">
            <div class="circle"></div>
          </div><div class="circle-clipper right">
            <div class="circle"></div>
          </div>
          </div>
        </div>
        <br>
        Cargando...
      </div>
      <div v-if="paso===1">
        <div>
          <div class="row">
            <div class="input-field col s12">
              <input value="I am not editable" placeholder="http://localhost:2962/WebPublicaciones.asmx" v-model="url" type="text" class="validate">
              <label >Direcci√≥n ASMX</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <textarea id="textarea1" rows="8" v-model="cabecera" :placeholder="cabeceraE" class="materialize-textarea"></textarea>
              <label for="textarea1">Cabecera XML</label>
            </div>
          </div>
        </div>
        <button type="button" class="btn btn-success" v-on:click="consulta">Consultar</button>
      </div>
      <div v-if="paso===2">
        <div class="section" id="index-banner2" >
          <div class="container">
            <div class="row" style="margin-bottom: 0;">
              <div class="col s12 m8 offset-m1 xl7 offset-xl1 center-on-small-only">
                <h1 class="header">Biblioteca</h1>
                <h4 class="light">Referencias incrustadas</h4>
                <h5 class="darken-1">Resultado</h5>
              </div>
            </div>
          </div>
        </div>
        <ul class="collapsible">
          <li v-for="(item,index) in lista" :key="index">
            <div class="collapsible-header">{{item.titulo_pu[0]}} <span class="text-info">{{item.autores_pu[0]}}</span></div>
            <div class="collapsible-body">
              <span class="text-justify">{{item.cita_pu[0]}}</span>
              <br>
              <a class="waves-effect waves-teal btn-flat" target="_blank" v-for="(boton,index2) in item.enlaces[0].enlaces" :key="index2" :href="boton.link_en[0]">{{boton.tipo[0].titulo_te[0]}}</a>
            </div>
          </li>
        </ul>
      </div>
      <div v-if="paso===3">
        <div class="alert alert-danger" role="alert">
          <h4 class="alert-heading">Error</h4>
          <p>No se pudo conectar con el Web Service</p>
          <hr>
          <p class="mb-0">Vuelve a intentar</p>
          <button type="button" class="btn btn-dark" v-on:click="paso=1">Atras</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  var parseString = require('xml2js').parseString;
export default {
  name: 'App',
  data:()=>({
    url:"puceing.edu.ec:15005/srobalino/Practica7/WebPublicaciones.asmx",
    cabecera:"<?xml version=\"1.0\" encoding=\"utf-8\"?>\n" +
      "<soap:Envelope xmlns:xsi=\"http://www.w3.org/2001/XMLSchema-instance\" xmlns:xsd=\"http://www.w3.org/2001/XMLSchema\" xmlns:soap=\"http://schemas.xmlsoap.org/soap/envelope/\">\n" +
      "  <soap:Body>\n" +
      "    <Publicaciones xmlns=\"http://tempuri.org/\" />\n" +
      "  </soap:Body>\n" +
      "</soap:Envelope>",
    cabeceraE:"<?xml version=\"1.0\" encoding=\"utf-8\"?>\n" +
      "<soap:Envelope xmlns:xsi=\"http://www.w3.org/2001/XMLSchema-instance\" xmlns:xsd=\"http://www.w3.org/2001/XMLSchema\" xmlns:soap=\"http://schemas.xmlsoap.org/soap/envelope/\">\n" +
      "  <soap:Body>\n" +
      "    <Publicaciones xmlns=\"http://tempuri.org/\" />\n" +
      "  </soap:Body>\n" +
      "</soap:Envelope>",
    lista:null,
    cargando:false,
    paso:0,
  }),
  methods:{
    consulta:function(){
      this.paso=0;
      let aux1;
      let aux2;
      axios.defaults.headers.post['Content-Type'] = 'text/xml';
      axios.post(this.url,this.cabecera)
        .then(res=>{
          let self = this;
          parseString(res.data, function (err, result) {
            aux1 = result["soap:Envelope"]["soap:Body"];
            aux2 = aux1[0]["PublicacionesResponse"][0]["PublicacionesResult"];
            self.lista=aux2[0]["publicaciones"];
          });
          this.paso=2;
        }).catch(err=>{
          console.log(err)
        this.paso=3;
        });
    }
  },
  mounted() {
    this.consulta();
  },
  updated() {
    M.updateTextFields();
    var elems = document.querySelectorAll('.collapsible');
    M.Collapsible.init(elems);
  }

}
</script>

<style>
  @import '../node_modules/materialize-css/dist/css/materialize.min.css';
  #index-banner {
    border-bottom: 1px solid rgba(0,0,0,0.14);
  }
  #index-banner2 {
    border-bottom: 1px solid rgba(0,0,0,0.14);
  }
  .loader {
    position: absolute;
    top :0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
</style>



